    height: 56px;
    font-size: 16px; }
  @include m {
    min-width: 160px; }
  &:before {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: -1;
    height: 300%;
    border-radius: 50% 50% 0 0/75% 75% 0 0;
    transition: height .35s; }
  &:hover:before {
    height: 0; }
  &_dark {
    border-color: $dark;
    color: $wh;
    &:before {
      background: $dark; }
    &:hover {
      color: $dark; } }
  &_primary {
    border-color: $primary;
    color: $wh;
    &:before {
      background: $primary; }
    &:hover {
      color: $primary; } }
  &_white {
    border-color: $wh;
    color: $primary;
    &:before {
