[data-aos="animation-scale-y"].aos-animate { transform: translateY(0) scaleY(1); opacity: 1; }

.productcard { margin: 0 5% 10% 5%; background-repeat: no-repeat; background-size: cover; border: 4px solid #FFFFFF; box-sizing: border-box; box-shadow: -5px 5px 10px rgba(241, 241, 241, 0.2), 5px -5px 10px rgba(238, 238, 238, 0.2), -5px -5px 10px rgba(255, 255, 255, 0.9), 5px 5px 13px rgba(236, 236, 236, 0.9), inset 1px 1px 2px rgba(255, 255, 255, 0.3), inset -1px -1px 2px rgba(214, 214, 214, 0.5); border-radius: 60px; }

.productcard:hover { border: 4px solid #FAFAFA; box-shadow: -5px 5px 10px rgba(200, 200, 200, 0.2), 5px -5px 10px rgba(200, 200, 200, 0.2), -5px -5px 10px rgba(200, 200, 200, 0.9), 5px 5px 13px rgba(200, 200, 200, 0.9), inset 1px 1px 2px rgba(200, 200, 200, 0.3), inset -1px -1px 2px rgba(214, 214, 214, 0.5); }

.industryicon { padding: 20px; border: 4px solid #FFFFFF; box-sizing: border-box; box-shadow: -5px 5px 10px rgba(241, 241, 241, 0.2), 5px -5px 10px rgba(238, 238, 238, 0.2), -5px -5px 10px rgba(255, 255, 255, 0.9), 5px 5px 13px rgba(236, 236, 236, 0.9), inset 1px 1px 2px rgba(255, 255, 255, 0.3), inset -1px -1px 2px rgba(214, 214, 214, 0.5); border-radius: 30px; }

.featuretile { background-color: #fff; box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.1); border-radius: 20px; display: flex; flex-direction: row; padding: 30px; max-width: 400px; min-height: 130px; margin: 20px 10px; transition: all 0.5s ease; color: #232340; }

.featuretile:hover { box-shadow: 8px 28px 50px rgba(39, 44, 49, 0.07), 1px 6px 12px rgba(39, 44, 49, 0.04); transform: translate3D(0, -1px, 0) scale(1.02); transition: all 0.5s ease; }

.featuretileimage { width: 80px; height: 80px; object-fit: cover; object-position: 50% 50%; border-radius: 100%; }

.featuretilemargin { margin-right: 12.5px; }
